import {_rec_routes} from './recurrence-router.js'
import login from "../views/login.vue";
import Home from "../views/Home.vue";

var _routes = [
	//##rootroot
	//##namepathname
	//##root-1push(root-1)
	//##
	//##subcoms
	
	//
	
	{
		path: '/',
		name: "login",
		component: login,
		root: [],
	},
	{
		path: "/home",
		name: "Home",
		component: Home,
		root: [],
		children: [
			/* 
			
			
				//
				//##root
				//##root-1/homechildrenroot-1
				//##
				//navicon.typeimgfont
				//navicon.iconicon
				
				 
			*/
			..._rec_routes,
			/* 
			
			
				
				
				
				
				//bugquerystorage
				
				//
				
					subpageshowleft:10
					isform:11(1,subpageshowleft10)
						
						import {ifleftclick} from '../../assets/js/common.js'ifleftclick
						
						beforeRouteLeave(to,from,next) {//
							if(from.meta.hasOwnProperty('isform')){
								if(from.meta.isform == 1&&ifleftclick){
									next()
								}else{
									var l = confirm('')
									if(l){
										next()
									}
								}
							}
						}
				
				//subpageshowleft
				
				vue3 admin
			
			
			*/
			//title
			{
				path: 'table_sub',
				name: 'table_sub',
				root: -1,
				meta:{
					title:''//
				},
				component: () => import( /* webpackChunkName: "about" */ "../views/_.vue"),
				children: [{
						path: "form_:type",
						name: "form-edit",
						root: -1,
						meta:{
							subpageshowleft:1,
							isform:1,
						},
						component: () => import( /* webpackChunkName: "about" */
							"../views/form-edit/index.vue"),
					},
					{
						path: "form2_:type",
						name: "form-edit2",
						root: -1,
						meta:{
							subpageshowleft:0,
							isform:0,
						},
						component: () => import( /* webpackChunkName: "about" */
							"../views/form-edit/index2.vue"),
					},
				]
			},
		]
	},
	//iframe
	{
		path: '/admindoc',
		root: -1,
		component: () => import('../views/project-doc/index.vue')
	},
	{
		path: '/chatpage',
		root: -1,
		component: () => import('../views/chatpage/chatpage.vue')
	}
]

export {
	_routes
}
